<template>
  <div>
    <div class="geren">
      <div class="dingbu">
        <div class="grinfo">
          <div class="tx">
            <img @click="bigImg" :src="photo" alt="">
          </div>
          <span class="" @click="()=>{this.$router.push({path:'/userInfo'})}">{{this.nickname}}</span>
        </div>
        <div class="level">
          {{this.levelname}}会员
        </div>
      </div>
      <div class="grlist_wrap">
        <ul class="grlist">
          <router-link tag="li" to="/bxYuyue">
            <img src="@/assets/img/gerenzhongxin--baoxianyuyue.png" alt="">
            <p>保险预约</p>
          </router-link>
          <router-link tag="li" to="/IncomeProfile">
            <img src="../../../assets/img/gerenzhongxin--wodeshouyi.png" alt="">
            <p>我的收益</p>
          </router-link>
          <router-link tag="li" to="/mallOrder">
            <img src="@/assets/img/gerenzhongxin--shangchengdingdan.png" alt="">
            <p>商城订单</p>
          </router-link>

          <router-link tag="li" to="/jfMingxi">
            <img src="../../../assets/img/gerenzhongxin--wodejifen.png" alt="">
            <p>我的积分</p>
          </router-link>
          <router-link tag="li" to="/CountManage">
            <img src="@/assets/img/gerenzhongxin--zhanghuguanli.png" alt="">
            <p>账号管理</p>
          </router-link>
          <li @click="shareApp">
            <img src="@/assets/img/gerenzhognxin--yaoqinghaoyou.png" alt="">
            <p>邀请好友</p>
          </li>
        </ul>
      </div>
      <van-button type="default btn_back" @click="logout">退出登录</van-button>

      <a href="" class="kefu iconfont icon-kefu"></a>
      <ml-i-view v-model="imgShow" :url="url" :scale="4"></ml-i-view>
    </div>
    <div class="footer">
      <ul>
        <router-link to = "/home" tag = "li">
          <span class="iconfont icon-yemian-copy1"></span>
          <p>首页</p>
        </router-link>
        <router-link to = "/mall" tag = "li">
          <span class="iconfont icon-shangcheng"></span>
          <p>商城</p>
        </router-link>
        <router-link to = "/user" tag = "li">
          <span class="iconfont icon-wode-"></span>
          <p>我的</p>
        </router-link>
      </ul>
    </div>
  </div>

</template>

<script>
  import { Toast } from "vant";
  import { apiGet} from "@/common/js/api";
  import { initWxConfig, isweixin } from "@/assets/utils"
  export default {
        name: "login",
        data(){
          return{
            levelname:'',
            nickname:'',
            photo:'',
            imgShow:false,
            url:'',
            wxConfig:{}

          }
        },
        created(){
          console.log(window.location.href.split('#')[0])
          let _this = this;
          apiGet("/index/sign-package",{
            url:encodeURIComponent("http://anfum.demo.sclonsee.com/user/login")
          }).then(res=>{
            let wxConfig = res.data.data.signpackage;
            wx.config({
                debug: false, // 开启调试模式,开发时可以开启
                appId: wxConfig.appId,   // 必填，公众号的唯一标识   由接口返回
                timestamp: wxConfig.timestamp, // 必填，生成签名的时间戳 由接口返回
                nonceStr: wxConfig.nonceStr,    // 必填，生成签名的随机串 由接口返回
                signature: wxConfig.signature,   // 必填，签名 由接口返回
                jsApiList: ["onMenuShareAppMessage","checkJsApi"] // 此处填你所用到的方法 
              });
              wx.ready(function(resdata){
                console.log('ready',resdata)
                console.log('userId',_this.$cookie.get('id'))
                wx.onMenuShareAppMessage({ // 分享给朋友
                  title: "安富智享 用户邀请",       // 分享标题
                  desc: "注册加入安富智享，体验专业的保险服务",   // 分享描述
                  link: `http://anfum.demo.sclonsee.com/register?shareId=${_this.$cookie.get('id')}`,       // 分享链接 默认以当前链接
                  imgUrl:'http://pbu0dcbw1.bkt.clouddn.com/yao_anfu.jpg',// 分享图标
                  // 用户确认分享后执行的回调函数
                  success: function () {
                    // Toast("分享成功！")
                  },
                  // 用户取消分享后执行的回调函数
                  cancel: function () {
                    // Toast("分享失败!")
                  },
                  fail: function (res) {
                    alert(JSON.stringify(res));
                  }
                }); 
              })
          })
         
        },
        mounted(){
          let token=this.$cookie.get('accessToken');
          apiGet('/user/info',{accessToken:token}).then((res)=>{
            console.log(res)
            this.levelname= res.data.data.grade_name;
            this.nickname=res.data.data.nickname;
            this.photo=res.data.data.photo;
          })
        },
        methods:{
          logout(){
            this.$cookie.delete('accessToken');
            this.$cookie.delete('id');
            this.$router.push('/user');
            this.$cookie.clear();
          },
          bigImg(){
              this.url=this.photo;
              this.imgShow=true
          },
          //邀请好友
          shareApp(){
            console.log(wx)
            
            if(isweixin()){
              // const { wxConfig } = this
              // const _this = this;
              
            }else{
              Toast("请在微信浏览器中打开")
            }
          }
        }
    }
</script>

<style scoped lang="scss">
  .footer{
    position: fixed;
    bottom: 0;
    .iconfont{
      font-size: 0.26rem;
      color: #BAC0CA;
    }
    p{
      color: #BAC0CA;
    }
    .router-link-active .iconfont,.router-link-active p{
      color:#4E8DF4;
    }
  }
  .geren{
    height: 100%;
    background: #E6E9ED;
    padding: 0 0 .75rem 0;
    overflow: auto;
    .dingbu{
      height: 2.13rem;
      position: relative;
      width: 100%;
      background-image:url("../../../assets/img/gerenzhongxin--bg.png") ;
      background-size: 100% 2.13rem;
      .grinfo{
        position: absolute;
        left: 0.17rem;
        bottom: 0.5rem;
        display: flex;
        .tx{
          width: 0.7rem;
          height: 0.7rem;
          padding: 0;
          border-radius: 0.7rem;
          img{width: 100%;height: 100%;border-radius: 0.63rem;display: block}
        }
        span{
          font-size: 0.19rem;
          color: white;
          line-height: 0.7rem;
          margin-left: 0.15rem;
        }
      }
      .level{
        width: 0.78rem;
        height: 0.31rem;
        line-height: 0.31rem;
        text-align: center;
        position: absolute;
        right: 0;
        bottom: 0.8rem;
        font-size: 0.13rem;
        color: white;
        background: #F5C622;
        border-bottom-left-radius: 0.16rem;
        border-top-left-radius: 0.16rem;
      }
    }
    .grlist_wrap{
      background: white;
      .grlist{
        display: flex;
        flex-wrap: wrap;
        li{
          height: 1.25rem;
          width: 33%;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          img{
            width: 0.36rem;
            height: 0.36rem;
          }
          p{
            color: #333;
            margin-top: 0.13rem;
            font-size: 0.15rem;
          }
        }
      }
    }
    .btn_back{
      display: block;
      width: 3.06rem;
      height: 0.47rem;
      font-size: 0.19rem;
      line-height: 0.47rem;
      color: white;
      text-align: center;
      margin: 0.61rem auto;
      background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
    }
  }
</style>



